import config from './config'

/**
 * 
 * @returns 
 */
export function $scanCode() {
    return new Promise((resolve, reject) => {
        uni.scanCode({
            success: ({ result }) => resolve(result),
            fail: err => reject(err)
        })
    })
}

/**
 * 
 */
export let $getStaticUrl = (url) => config.STATIC_URL + url

/**
 * 
 * @param {*} duration 
 * @param {*} scrollTop 
 * @returns 
 */
export function $go2Top(duration = 300, scrollTop = 0) {
    return new Promise(resolve => {
        uni.pageScrollTo({
            scrollTop,
            duration,
            success: r => resolve(r)
        })
    })
}

/**
 * 
 * @param {*} data 
 * @returns 
 */
export function $copy(data) {
    return new Promise(resolve => {
        uni.setClipboardData({
            data,
            success: r => resolve(r)
        });
    })
}

/**
 * ActionSheet
 * @param {Object} itemList 
 * @return {i,text} 
 * 
    let {i, text } = await this.$showActionSheet(['', ''])
 */
export function $showAction(itemList) {
    return new Promise(resolve => {
        uni.showActionSheet({
            itemList,
            success: ({ tapIndex }) => {
                resolve({
                    i: tapIndex,
                    text: itemList[tapIndex]
                });
            }
        })
    })
}

/**
 * 
 * @param {*} title 
 */
export function $setTitle(title) {
    uni.setNavigationBarTitle({ title })
}

/**
 * 
 */
export function $call(phoneNumber) {
    uni.makePhoneCall({ phoneNumber })
}

/**
 * 
 */
export function $showMap({ lat, lng }) {
    return new Promise((resolve, reject) => {
        uni.openLocation({
            latitude: parseFloat(lat),
            longitude: parseFloat(lng),
            success(e) {
                resolve(e)
            },
            fail(e) {
                reject(e);
            }
        })
    })
}

/**
 * 
 * */
export function $previewImg(urls, current) {
    if (!Array.isArray(urls)) {
        urls = [urls]
        current = 0
    }
    uni.previewImage({
        urls,
        current,
    })
}